// axios
import theAxios from 'axios'
import router from '@/router/index.js'
// import { Notify } from 'vant'
import { getToken, removeToken } from '@/utils/token.js' //, setToken
// import { getNewTokenAPI } from '@/api/index.js'

const axios = theAxios.create({
  // 
  baseURL: 'http://toutiao.itheima.net',
  // 2020
  timeout: 20000
})

// 
axios.interceptors.request.use(
  (config) => {
    // config urlheaders
    // 
    // tokenheaders
    // tokenapi/index.jsAuthorization
    // ?. length,undefined
    // getToken()(token)length
    if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
      config.headers.Authorization = `Bearer ${getToken()}`
      // console.log(config)
    }
    return config
  },
  (error) => {
    // 
    return Promise.reject(error)
  }
)

// 
// 
axios.interceptors.response.use(
  (response) => {
    // http2xx,3xx
    // 
    return response
  },

  async (error) => {
    // http4xx,5xx
    // 
    console.dir(error)
    // 401(token)
    if (error.response.status === 401) {
      // 1:token,,
      // , -> 
      // Notify({ type: 'warning', message: '' })
      removeToken() // token,,
      // router.currentRoute  vue this.$route -> 
      // fullpath, #
      router.replace(`/login?path=${router.currentRoute.fullPath}`)
      //  this.$router(thisvue$router)
      // this.$router router@/routerrouter
    }

    // // 2:refresh_token,,
    // // token,,()
    // const res = await getNewTokenAPI()
    // console.log(res, 'token')
    // // token,?
    // // 1. token
    // setToken(res.data.data.token)
    // // 2. token
    // error.config.headers.Authorization = `Bearer ${res.data.data.token}` // Bearer 
    // // 3. ,
    // // error.config 
    // // return - returnPromise
    // return axios(error.config)
    // } else if (
    //   error.response.status === 500 &&
    //   error.config.url === '/v1_0/authorizations' &&
    //   error.config.method === 'put'
    // ) {
    //   // refresh_token
    //   localStorage.clear() // localStorage
    //   Notify({ type: 'warning', message: '!!!' })
    //   router.replace('/login')
    // }

    return Promise.reject(error)
  }
)

export default axios
