// - 
export const removeTheChannelAPI = ({ channelId }) => {
  return request({
    url: `/v1_0/user/channels/${channelId}`,
    method: 'DELETE'
  })
}

//  - 
export const getAllArticleListAPI = ({ channel_id, timestamp }) => {
  return request({
    url: '/v1_0/articles',
    method: 'GET',
    // params ,axiosURL?()
    params: {
      channel_id,
      timestamp
    }
  })
}

//  -  - 
export const dislikeArticleAPI = ({ artId }) => {
  return request({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    // post
    data: {
      target: artId
    }
  })
}

//  -  - 
export const reportArticleAPI = ({ artId, type }) => {
  return request({
    url: '/v1_0/article/reports',
    method: 'POST',
    // post
    data: {
      target: artId,
      type: type,
      remark:
        '0'
    }
  })
}

//  - 
export const suggestListAPI = ({ keywords }) => {
  return request({
    url: '/v1_0/suggestion',
    method: 'GET',
    params: {
      q: keywords
    }
  })
}

//  - 
export const searchResultAPI = ({ page = 1, per_page = 10, q }) => {
  return request({
    url: '/v1_0/search',
    method: 'GET',
    params: {
      page,
      per_page,
      q
    }
  })
}
