//  -  - 
export const commentSendAPI = ({ id, content, art_id = null }) => {
  // 1. axiosdatanull
  // (paramsnull)
  // 2. art_idart_id
  // art_idnull

  // data
  const obj = {
    target: id,
    content
  }
  if (art_id !== null) {
    // 
    obj.art_id = art_id
  }
  return request({
    url: '/v1_0/comments',
    method: 'POST',
    data: obj
  })
}

